import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';

import { CourseService } from '../../angular-services/course.service';
import { CourseCategoryService } from '../../angular-services/course-category.service';
import { CourseCategory } from '../../classes/course-category';
import { Course } from '../../classes/course';

@Component({
  selector: 'app-edit-course-form-admin',
  templateUrl: './edit-course-form.component.html',
  styleUrls: ['./edit-course-form.component.css']
})
export class EditCourseFormComponent implements OnInit {

  @Output() courseTitle = new EventEmitter<string>();
  formEditCourse: FormGroup;
  alert: any = { type: '', message: '', show: false };
  categories: CourseCategory[] = [];
  course: Course = new Course();
  mediaType = 'courses';
  urlObjects: string = 'https://viruscursinho.com.br:3002/courses';
  showModalSelectFile: boolean = false;

  constructor(private courseService: CourseService, private categoryService: CourseCategoryService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.initForm();
    this.getCategories();
    this.getCourse();
  }

  initForm() {
    this.formEditCourse = new FormGroup({
      title: new FormControl('', Validators.required),
      description: new FormControl('', Validators.required),
      photo: new FormControl(''),
      idCategory: new FormControl('', Validators.required),
      status: new FormControl('', Validators.required),
    });
  }

  getCourse() {
    let key = this.route.snapshot.paramMap.get('id');
    this.courseService.getCourse(key).subscribe(
      response => {
        if (response !== undefined) {
          this.course = response.course;
          this.course.idCategory = response.category._key;
          this.courseTitle.emit(this.course.title);
          this.loadForm();
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  loadForm() {
    this.formEditCourse.patchValue({
      title: this.course.title,
      description: this.course.description,
      idCategory: this.course.idCategory,
      status: this.course.status,
      photo: this.course.photo
    });
  }

  save() {
    if (this.formEditCourse.valid) {
      let key = this.route.snapshot.paramMap.get('id');
      let data = this.formEditCourse.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.courseService.updateCourse(key, data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

  getCategories() {
    this.categoryService.getCategories().subscribe(
      response => {
        if (response !== undefined) {
          for (let category of response) {
            if (category.status) {
              this.categories.push(category);
            }
          }
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  changeStatusModalSelectFile() {
    if (this.showModalSelectFile) {
      this.showModalSelectFile = false;
    } else {
      this.showModalSelectFile = true;
    }
  }

  setUrlObject(link) {
    this.formEditCourse.patchValue({
      photo: link
    });
    this.showModalSelectFile = false;
  }

}
